import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography
} from "@mui/material";

export default function BasicTextFields() {
  return (
    <div style={{ display: "flex" }}>
      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="550"
          image="over dose1.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h7" component="div"></Typography>
          <Typography gutterBottom variant="h8" component="div">
            なとり Over dose
          </Typography>
          <Typography variant="body2" color="text.secondary">
            Tik Tokの音源でよく使われてる有名な曲。「Over
            dose」とは、薬物などの過剰摂取を意味し、やめられなくなった、薬が欲しくてたまらない
            様子が歌われている楽曲。一度聴くと頭から離れない癖のあるカッコいい曲です。
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Share</Button>
          <Button size="big">お気に入り</Button>
        </CardActions>
      </Card>

      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="550"
          image="Amazora.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h7" component="div"></Typography>
          <Typography gutterBottom variant="h7" component="div">
            AAA 恋音と雨空
          </Typography>
          <Typography variant="body2" color="text.secondary">
            AAAといえばこの曲。切なくてストレートな歌詞でカラオケランキングでも上位に入る代表失恋ソングであり、片思いソングでもある。メロディーがきれいで、大人の恋を連想させる曲です。なに聴くか迷ったら、ぜひ聴いてみてください。
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Share</Button>
          <Button size="big">お気に入り</Button>
        </CardActions>{" "}
      </Card>

      <Card sx={{ maxWidth: 630 }}>
        <CardMedia
          component="img"
          height="550"
          image="beteru.jpg"
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h7" component="div"></Typography>
          <Typography gutterBottom variant="h7" component="div">
            優理 ベテルギウス
          </Typography>
          <Typography variant="body2" color="text.secondary">
            ドラマ「SUPER
            RICH」の主題歌として歌われており、人と人との繋がりや絆がテーマになっている曲。YouTubeに公開されてから数日で300万回再生突破するほど話題になった。いろんなことが起こる人生の中で信頼できる誰かへの思いが描かれている。
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="small">Share</Button>
          <Button size="big">お気に入り</Button>
        </CardActions>
      </Card>
    </div>
  );
}
